'use client'

import { useState } from 'react'
import { useSearchParams, useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { CheckCircle, ArrowLeft, Play } from 'lucide-react'

export default function AppPage() {
  const searchParams = useSearchParams()
  const router = useRouter()
  const slug = searchParams.get('slug') || ''
  const [isCompleted, setIsCompleted] = useState(false)

  const handleCompleteDemoQuiz = () => {
    try {
      // Simulate quiz completion in demo
      setIsCompleted(true)
      
      // Redirect back to home after a delay
      setTimeout(() => {
        router.push('/')
      }, 3000)
    } catch (error) {
      console.error('Error completing demo quiz:', error)
    }
  }

  const handleBackToHome = () => {
    try {
      router.push('/')
    } catch (error) {
      console.error('Error navigating to home:', error)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 via-white to-blue-50 flex flex-col">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-4xl mx-auto px-4 py-4 flex items-center gap-4">
          <Button
            variant="outline"
            size="sm"
            onClick={handleBackToHome}
            className="flex items-center gap-2"
          >
            <ArrowLeft className="w-4 h-4" />
            Back to Home
          </Button>
          <div className="flex-1 text-center">
            <h1 className="text-xl font-bold text-gray-800">
              PWA Quiz Demo
            </h1>
            {slug && (
              <p className="text-sm text-gray-600 mt-1">
                Demo Slug: {slug}
              </p>
            )}
          </div>
          <div className="w-20"></div> {/* Spacer for centering */}
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex items-center justify-center p-4">
        <Card className="w-full max-w-md shadow-lg border-2 border-green-200 bg-white">
          <CardHeader className="text-center">
            <div className="w-20 h-20 bg-gradient-to-br from-green-500 to-emerald-500 rounded-full flex items-center justify-center mx-auto mb-4">
              {isCompleted ? (
                <CheckCircle className="w-10 h-10 text-white" />
              ) : (
                <Play className="w-10 h-10 text-white" />
              )}
            </div>
            <CardTitle className="text-2xl font-bold text-gray-800">
              {isCompleted ? 'Demo Quiz Completed!' : 'Demo Quiz Experience'}
            </CardTitle>
            <CardDescription className="text-lg text-gray-600">
              {isCompleted 
                ? 'Thank you for trying the demo. Redirecting to home...'
                : 'This is a simplified demo version of the quiz system.'}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {!isCompleted ? (
              <>
                <div className="bg-gradient-to-r from-green-50 to-emerald-50 p-6 rounded-lg border border-green-200">
                  <h3 className="font-semibold text-green-800 mb-3 text-center">
                    ðŸŽ¯ Demo Quiz Features
                  </h3>
                  <ul className="text-sm text-green-700 space-y-2">
                    <li className="flex items-center gap-2">
                      <span className="w-2 h-2 bg-green-500 rounded-full"></span>
                      Instant completion simulation
                    </li>
                    <li className="flex items-center gap-2">
                      <span className="w-2 h-2 bg-emerald-500 rounded-full"></span>
                      No external quiz dependencies
                    </li>
                    <li className="flex items-center gap-2">
                      <span className="w-2 h-2 bg-blue-500 rounded-full"></span>
                      Modern PWA interface
                    </li>
                    <li className="flex items-center gap-2">
                      <span className="w-2 h-2 bg-purple-500 rounded-full"></span>
                      Simplified user flow
                    </li>
                  </ul>
                </div>
                
                <Button 
                  onClick={handleCompleteDemoQuiz}
                  className="w-full bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600 text-white py-4 text-lg font-semibold h-14 transition-all duration-300 transform hover:scale-105 shadow-lg"
                >
                  âœ… Complete Demo Quiz
                </Button>
              </>
            ) : (
              <div className="space-y-4">
                <div className="bg-green-50 p-6 rounded-lg border border-green-200 text-center">
                  <div className="animate-pulse mb-4">
                    <div className="flex items-center justify-center">
                      <ArrowLeft className="w-6 h-6 text-green-600 mr-2" />
                      <span className="text-green-800 font-medium">Redirecting to home...</span>
                    </div>
                  </div>
                  <p className="text-sm text-green-700">
                    Demo completed successfully! You will be redirected automatically.
                  </p>
                </div>
                
                <Button 
                  onClick={handleBackToHome}
                  variant="outline"
                  className="w-full"
                >
                  Go to Home Now
                </Button>
              </div>
            )}

            <div className="text-center text-xs text-gray-500 border-t pt-4 mt-6">
              <p className="font-medium">Demo Mode Active</p>
              <p className="mt-1">This is a simplified demonstration of the quiz system</p>
              {slug && (
                <p className="mt-1 text-blue-600">Processing demo slug: {slug}</p>
              )}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
